import React from 'react';

const inputStyle = {
	backgroundColor: '#2f88fc',
	borderColor: '#2f88fc',
	color: 'white',
}

const buttonStyle = {
	backgroundColor: '#60460f',
	border: '0px',
	borderRadius: '12px',
	width: '20%',
	margin: 'auto',
	color: 'white',
}

const Form = props => (
	<form onSubmit={props.getWeather}>
		<input style={inputStyle} type="text" name="city" defaultValue="Ottawa"/>
		<input style={inputStyle} type="text" name="country" defaultValue="Ca"/>
		<button style={buttonStyle}>Get Weather</button>
	</form>
);

export default Form;