import React from 'react';
import { Route, Link } from 'react-router-dom';
import FiveDayForcast from './Components/fiveDayForcast';
import CurrentWeather from './Components/currentWeather';


export default class App extends React.Component {
  render(){
		const imgUrl = 'https://eus-www.sway-cdn.com/s/lS9lnvgYelIJHUrK/images/AJBYoWDmgsV86b?quality=960&allowAnimation=false'
		
		const appStyle = {
			width: '634px',
			height: '350px',
			backgroundImage: 'url('+ imgUrl +')',
			backgroundSize: 'cover',
			border: 'solid',
			borderRadius: '12px',
			fontFamily: 'Open Sans, sans-serif',
		}
		
		const buttonStyle = {
			backgroundColor: '#60460f',
			border: '0px',
			borderRadius: '12px',
			width: '25%',
			margin: 'auto',
		}
		
		const center = {
			display: 'flex',
			width: '100%',
			margin: 'auto',
		}
		
		const linkStyle = {
			color: 'white',
		}
		
    return (               
				<div style={appStyle}>
					<div style={center}>
						<button style={buttonStyle}><Link to='/' style={linkStyle}>FiveDayForcast</Link></button>
						<button style={buttonStyle}><Link to='/currentWeather' style={linkStyle}>CurrentWeather</Link></button>
					</div>
					
					<Route exact path='/' component={FiveDayForcast} />
					<Route path='/currentWeather' component={CurrentWeather} />
				</div>
    );
  }
}


